import { PropsWithChildren } from "react"
import { alpha } from "@rp/react/web/mui/core"
import { UseQueryResult } from "@tanstack/react-query"
import { WatchlistDataTableClickableCell } from "./WatchlistDataTableClickableCell"
import { WatchlistDataTableDataStatusCell } from "./WatchlistDataTableDataStatusCell"

type Props = PropsWithChildren<{
  value?: number | null
  tooltip?: string | React.ReactNode
  onClick?: () => void
  queryResult?: UseQueryResult<unknown, Error>
}>

function WatchlistDataTableColoredCell({ children, value, tooltip, queryResult, onClick }: Props) {
  const rawPrice = value ?? 0
  return (
    <WatchlistDataTableDataStatusCell queryResult={queryResult} value={value}>
      <WatchlistDataTableClickableCell
        onClick={onClick}
        tooltip={tooltip}
        hoverBackgroundColor={theme => {
          if (rawPrice > 0) return alpha(theme.palette.success.main, 0.2)
          if (rawPrice < 0) return alpha(theme.palette.error.main, 0.2)
          return alpha(theme.palette.text.primary, 0.2)
        }}
        color={theme => {
          if (rawPrice > 0) return theme.palette.success.main
          if (rawPrice < 0) return theme.palette.error.main
          return theme.palette.text.primary
        }}
      >
        {children}
      </WatchlistDataTableClickableCell>
    </WatchlistDataTableDataStatusCell>
  )
}

export { WatchlistDataTableColoredCell }
